@import url(https://fonts.googleapis.com/css?family=Abril+Fatface&display=swap);
body {
  background: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/385441/skulls.png"); }

.mainBox {
  position: relative;
  top: 4rem;
  background-color: #abd9ed;
  max-width: 700px;
  height: auto;
  text-align: center;
  margin: auto;
  border-radius: 6px;
  box-shadow: 5px 10px #888888; }
  .mainBox header {
    padding-top: 1rem; }
  .mainBox #text {
    color: #1c1c1a; }
  .mainBox button {
    height: 60px;
    width: 100px;
    border-radius: 10px;
    background: #e9e9dd;
    margin: 1rem;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out; }
    .mainBox button:hover {
      opacity: .95;
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
      background: #fff; }
  .mainBox #new-quote {
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out; }
    .mainBox #new-quote:hover {
      color: #f05728;
      opacity: .95;
      -webkit-transform: scale(1.1);
              transform: scale(1.1); }
  .mainBox #quote {
    padding-right: 16px;
    padding-left: 14px; }

footer {
  text-align: center;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2.5rem; }

h1 {
  font-family: Abril Fatface; }

